import { Grid, Box, Button } from "@mui/material";

import OpenInNewIcon from "@mui/icons-material/OpenInNew";

import { useState, useEffect } from "react";

import { socketSend } from "../App";

import "../styles/VideoPlayerComponent.css";

import "../styles/ScriptContentComponent.css";

import Mp4FilesListComponent from "./Mp4FilesListComponent";
import VideoPlayerComponent from "./VideoPlayerComponent";
import ScriptContentComponent from "./ScriptContentComponent";

import RefreshIcon from "@mui/icons-material/Refresh";

export default function SinglePanelComponent(props) {
  const {
    reutersFilesList,
    cnnFilesList,
    aptnFilesList,
    AllPressesDict,
    setLoadingState,
    isLoading,
    setSttStatus,
    sttStatus,
    setTranslateStatus,
    translateStatus,
    mp4FileListUpdater,
    setOnOffButtonStatus,
    onoffButtonStatus,
    mp4OpenButtonStatus,
    setMp4OpenButtonStatus,
    scriptOpenButtonStatus,
    setScriptOpenButtonStatus,
    scriptsListState,
    setScriptsListState,
    scriptContentState,
    setScriptContentState,
  } = props;

  const [selectedPress, setSelectedPress] = useState("ALL");
  const [videoUriState, setVideoUriState] = useState("");
  const [mp4FilesList, setMp4FilesList] = useState(
    selectedPress === "ALL"
      ? [...reutersFilesList, ...cnnFilesList, ...aptnFilesList].sort(
          (a, b) => {
            return (
              Date.parse("20" + b.dateTime) - Date.parse("20" + a.dateTime)
            );
          }
        )
      : selectedPress === "REUTERS"
      ? [...reutersFilesList]
      : selectedPress === "APTN"
      ? [...aptnFilesList]
      : [...cnnFilesList]
  );
  const [selectedMp4File, setSelectedMp4File] = useState(null);
  const [selectedScriptFile, setSelectedScriptFile] = useState("");
  const [press4Mp4file, setPress4Mp4file] = useState("ALL");

  useEffect(() => {
    setMp4FilesList(
      selectedPress === "ALL"
        ? [...reutersFilesList, ...cnnFilesList, ...aptnFilesList].sort(
            (a, b) => {
              return (
                Date.parse("20" + b.dateTime) - Date.parse("20" + a.dateTime)
              );
            }
          )
        : selectedPress === "REUTERS"
        ? [...reutersFilesList]
        : selectedPress === "APTN"
        ? [...aptnFilesList]
        : [...cnnFilesList]
    );
  }, [
    selectedPress,
    [...reutersFilesList],
    [...cnnFilesList],
    [...aptnFilesList],
  ]);

  useEffect(() => {
    setScriptsListState([]);
    setScriptContentState("");
    setVideoUriState("");
  }, [selectedPress]);

  useEffect(() => {
    if (selectedMp4File) {
      setLoadingState(true);
      setScriptContentState("");
      setSelectedScriptFile("");
      setVideoUriState(`${press4Mp4file.toLowerCase()}/${selectedMp4File}`);
      setLoadingState(false);
    }
  }, [selectedMp4File]);

  useEffect(() => {
    if (selectedScriptFile !== "" && press4Mp4file !== "ALL") {
      setScriptContentState("");

      setLoadingState(true);
      let fullDir = `d:\\agent_${press4Mp4file}\\`;
      socketSend(
        press4Mp4file,
        `<get_scdata><${fullDir + selectedScriptFile}>`
      );
      nowLog("Script Content Fetched for " + selectedScriptFile);

      setLoadingState(false);
    }
  }, [selectedScriptFile]);

  const toggleSTT = () => {
    setOnOffButtonStatus("disable");
    socketSend("reuter", `<set_stt_${sttStatus ? "off" : "on"}>`);
    socketSend("aptn", `<set_stt_${sttStatus ? "off" : "on"}>`);
    socketSend("cnn", `<set_stt_${sttStatus ? "off" : "on"}>`);

    setTimeout(() => {
      setSttStatus(!sttStatus);
      setOnOffButtonStatus("enable");
    }, 500);
  };

  const toggleTranslate = () => {
    setOnOffButtonStatus("disable");
    socketSend("reuter", `<set_trans_${translateStatus ? "off" : "on"}>`);
    socketSend("aptn", `<set_trans_${translateStatus ? "off" : "on"}>`);
    socketSend("cnn", `<set_trans_${translateStatus ? "off" : "on"}>`);
    setTimeout(() => {
      setTranslateStatus(!translateStatus);
      setOnOffButtonStatus("enable");
    }, 500);
  };

  return (
    <div>
      <Grid container>
        <Grid item>
          <TabButtons
            selectedPress={selectedPress}
            AllPressesDict={AllPressesDict}
            setSelectedPress={setSelectedPress}
            isLoading={isLoading}
            sttStatus={sttStatus}
            toggleSTT={toggleSTT}
            translateStatus={translateStatus}
            toggleTranslate={toggleTranslate}
            mp4FileListUpdater={mp4FileListUpdater}
            onoffButtonStatus={onoffButtonStatus}
          />
        </Grid>
        <Grid
          container
          direction="row"
          justifyContent="flex-start"
          alignItems={"flex-start"}
        >
          <Grid item>
            <Mp4FilesListComponent
              selectedPress={selectedPress}
              mp4filesListState={mp4FilesList}
              isLoading={isLoading}
              setLoadingState={setLoadingState}
              setPress4Mp4file={setPress4Mp4file}
              setSelectedMp4File={setSelectedMp4File}
              scriptsListState={scriptsListState}
              setSelectedScriptFile={setSelectedScriptFile}
              AllPressesDict={AllPressesDict}
              setScriptsListState={setScriptsListState}
              setMp4OpenButtonStatus={setMp4OpenButtonStatus}
              setScriptOpenButtonStatus={setScriptOpenButtonStatus}
            />
          </Grid>

          <Grid item>
            <Grid item>
              <VideoPlayerComponent
                videoUriState={videoUriState}
                AllPressesDict={AllPressesDict}
                press4Mp4file={press4Mp4file}
                mp4OpenButtonStatus={mp4OpenButtonStatus}
                setMp4OpenButtonStatus={setMp4OpenButtonStatus}
              />
            </Grid>
            <Grid item height={"40vh"}>
              <ScriptContentComponent
                scriptContentState={scriptContentState}
                press4Mp4file={press4Mp4file}
                AllPressesDict={AllPressesDict}
                scriptOpenButtonStatus={scriptOpenButtonStatus}
                selectedScriptFile={selectedScriptFile}
              />
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
}

function TabButtons(props) {
  const {
    selectedPress,
    setSelectedPress,
    isLoading,
    sttStatus,
    toggleSTT,
    toggleTranslate,
    translateStatus,
    mp4FileListUpdater,
    onoffButtonStatus,
  } = props;

  const urlParams = new URL(window.location.href).searchParams;
  const isAdmin = !!urlParams.get("admin");

  return (
    <Box>
      {["ALL", "REUTERS", "APTN", "CNN"].map((press, index) => {
        return (
          <>
            <Button
              variant={press === selectedPress ? "outlined" : "contained"}
              style={{
                backgroundColor: press === selectedPress ? "white" : "gray",
                marginLeft: "10px",
              }}
              onClick={() => {
                if (!isLoading) setSelectedPress(press);
              }}
            >
              {" "}
              {press}{" "}
              {press !== "ALL" ? (
                <button
                  style={{ border: 0, backgroundColor: "transparent" }}
                  onClick={() => {
                    alert(press);
                  }}
                >
                  <OpenInNewIcon fontSize="12px" />
                </button>
              ) : (
                ""
              )}
            </Button>
          </>
        );
      })}
      <p style={{ display: isAdmin ? "inline" : "none" }}>
        <Button
          viarant="contained"
          style={{
            backgroundColor: sttStatus !== true ? "#B0B0B0" : "red",
            fontSize: "15px",
            marginLeft: "50px",
          }}
          onClick={toggleSTT}
          disabled={onoffButtonStatus === "disable" ? true : false}
        >
          {sttStatus === null
            ? "STT  "
            : sttStatus
            ? "STT ON"
            : "STT OFF"}
        </Button>

        <Button
          style={{
            backgroundColor: translateStatus !== true ? "#B0B0B0" : "red",
            fontSize: "15px",
            marginLeft: "20px",
          }}
          onClick={toggleTranslate}
          disabled={onoffButtonStatus === "disable" ? true : false}
        >
          {translateStatus === null
            ? "  "
            : translateStatus
            ? " ON"
            : " OFF"}
        </Button>
      </p>
      <Button
        variant="contained"
        style={{
          fontSize: "15px",
          marginLeft: "20px",
        }}
        onClick={() => {
          console.log("refreshing");
          mp4FileListUpdater();
        }}
      >
        <RefreshIcon color="black"></RefreshIcon>
      </Button>
    </Box>
  );
}

const nowLog = (msg) => {
  let now = new Date(Date.now());
  now =
    now.getHours().toString() +
    "-" +
    now.getMinutes().toString() +
    "-" +
    now.getSeconds().toString();
  console.log(`${now} ${msg}`);
};
