import { Button, Grid } from "@mui/material";

import SaveAltRoundedIcon from "@mui/icons-material/SaveAltRounded";

export default function ScriptContentComponent(props) {
  const {
    scriptContentState,

    selectedScriptFile,
  } = props;
  return (
    <Grid container height="100%">
      <div className="scriptContentComponent">
        <textarea
          value={scriptContentState}
          readOnly
          style={{ width: "100%" }}
        />
        <div className="scriptButtons">
          <div className="scriptDownloadButton">
            <Button
              variant="contained"
              onClick={() => {
                var link = document.createElement("a");
                link.setAttribute(
                  "download",
                  selectedScriptFile.replaceAll("xml", "txt")
                );
                var blob = new Blob([scriptContentState], {
                  type: "text/plain",
                });
                link.href = window.URL.createObjectURL(blob);
                link.click();
              }}
            >
              <SaveAltRoundedIcon />
            </Button>
          </div>
        </div>
      </div>
    </Grid>
  );
}
