import { DataGrid, GridToolbar } from "@mui/x-data-grid";

import { Grid } from "@mui/material";

import "../styles/ScriptListComponent.css";

import * as ColumnDefs from "./defs/ColumnsDefs";

export default function ScriptsListComponent(props) {
  const { scriptsListState, setSelectedScriptFile, isLoading } = props;
  return (
    <Grid item xs={1}>
      <div item className="scriptList">
        <DataGrid
          rows={scriptsListState}
          disableColumnFilter
          disableColumnMenu
          disableColumnSelector
          disableDensitySelector
          columns={ColumnDefs.scriptFilesColumns}
          onRowClick={(params) => {
            setSelectedScriptFile(params.row.scriptFilename);
          }}
          density="compact"
          loading={isLoading}
          hideFooterSelectedRowCount
          slots={{ toolbar: GridToolbar }}
          slotProps={{
            toolbar: {
              showQuickFilter: false,
              quickFilterProps: { debounceMs: 500 },
            },
          }}
          columnVisibilityModel={{
            scriptFilename: false,
          }}
        />
      </div>
    </Grid>
  );
}
