import IosShareIcon from "@mui/icons-material/IosShare";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import CloudDoneIcon from "@mui/icons-material/CloudDone";
import ReportGmailerrorredIcon from "@mui/icons-material/ReportGmailerrorred";

import { Alert, Snackbar } from "@mui/material";

import "../../styles/buttonStyle.css";

import { useState } from "react";

export const filesColumns = [
  { field: "press", headerName: "", width: 100 },
  {
    field: "hash",
    headerName: "",
    width: 200,
    renderCell: (params) => {
      return (
        <div>
          <CopyButton target={params.row.hash}></CopyButton>
        </div>
      );
    },
  },
  {
    field: "title",
    headerName: "",
    width: 450,
    renderCell: (params) => {
      return (
        <div>
          <CopyButton target={params.row.title}></CopyButton>
        </div>
      );
    },
  },
  { field: "dateTime", headerName: "", width: 150 },
  {
    field: "isSent",
    headerName: ``,
    width: 80,
    renderCell: (params) => {
      return params.row.isSent ? (
        <CloudDoneIcon style={{ margin: "auto" }}></CloudDoneIcon>
      ) : (
        <button
          variant="contained"
          onClick={(event) => {
            forceEncodeClickHandler(params);
          }}
        >
          <IosShareIcon />
        </button>
      );
    },
  },
  {
    field: "ingestStatus",
    headerName: `NDS`,
    width: 60,
    renderCell: (params) => {
      return params.row.isIngested === "succeeded" ? (
        <CloudDoneIcon></CloudDoneIcon>
      ) : params.row.isIngested === "error" ? (
        <ReportGmailerrorredIcon></ReportGmailerrorredIcon>
      ) : null;
    },
  },
  { field: "filename", headerName: "", width: 0 },
];

export const scriptFilesColumns = [
  { field: "id", headerName: "()", width: 100 },
  { field: "hash", headerName: "", width: 200 },
  { field: "dateTime", headerName: "", width: 150 },
  { field: "scriptFilename", headerName: "  ", width: 0 },
];

// const urlHeader = "http://10.10.108.225:8080/get";
const urlHeader = "/get";
const POSTOption = { method: "POST", credentials: "include" };

function forceEncodeClickHandler(params) {
  fetch(
    urlHeader +
      params.row.press.toLowerCase() +
      "forceEncode/" +
      params.row.filename,
    POSTOption
  );
}

function forceIngestClickHandler(params) {}

function CopyButton(props) {
  const [open, setOpen] = useState(false);
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpen(false);
  };
  const [copyTarget, setCopyTarget] = useState(null);

  return (
    <div>
      <button
        style={{ outline: "none", border: 0, backgroundColor: "transparent" }}
        onClick={(event) => {
          console.log(props.target);
          window.navigator.clipboard.writeText(props.target);
          setOpen(true);
          setCopyTarget(props.target);
        }}
      >
        <ContentCopyIcon fontSize="8px" />
      </button>
      {props?.target?.toString()}
      <Snackbar
        open={open}
        autoHideDuration={700}
        onClose={handleClose}
        anchorOrigin={{ vertical: "top", horizontal: "left" }}
      >
        <Alert
          onClose={handleClose}
          severity="info"
        >{`${copyTarget} .`}</Alert>
      </Snackbar>
    </div>
  );
}
