import { Grid } from "@mui/material";

const HOST_MAIN = "202.31.153.221";
const HOST_BACKUP = "202.31.153.222";

export default function VideoPlayerComponent(props) {
  const { videoUriState } = props;

  return (
    <Grid item xs={1}>
      <div item className="videoComponent">
        <video
          src={`http://${HOST_MAIN}:50080/` + encodeURI(videoUriState)}
          controls
          preload="true"
        />
        <div item className="mp4Buttons"></div>
      </div>
    </Grid>
  );
}
