import "../styles/Mp4FilesListComponent.css";

import { DataGrid, GridToolbar } from "@mui/x-data-grid";

import { Grid, Menu, MenuItem } from "@mui/material";

import * as ColumnDefs from "./defs/ColumnsDefs";

import { useState } from "react";

import ScriptsListComponent from "./ScriptsListComponent";

import { socketSend } from "../App";

export default function Mp4FilesListComponent(props) {
  const {
    mp4filesListState,
    isLoading,
    setLoadingState,
    setSelectedMp4File,
    setPress4Mp4file,
    selectedPress,
    scriptsListState,
    setSelectedScriptFile,
    setScriptsListState,
    setMp4OpenButtonStatus,
    setScriptOpenButtonStatus,
  } = props;

  const [anchorEl, setAnchorEl] = useState(null);

  const open = Boolean(anchorEl);

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <Grid item container>
      <div className="mp4FileList">
        <DataGrid
          id="mp4FileGrid"
          rows={mp4filesListState}
          disableColumnMenu
          disableColumnSelector
          disableDensitySelector
          columns={ColumnDefs.filesColumns}
          density="compact"
          loading={isLoading}
          hideFooterSelectedRowCount
          slots={{ toolbar: GridToolbar }}
          onRowClick={(params, event) => {
            console.log(params);
            setMp4OpenButtonStatus(true);
            setScriptOpenButtonStatus(true);
            setLoadingState(true);
            setPress4Mp4file(params.row.press);
            setSelectedMp4File(params.row.filename);
            (() => {
              setScriptsListState([]);
              socketSend(
                params.row.press,
                `<get_script><${params.row.filename}>`
              );
              setLoadingState(false);
            })();
            setAnchorEl(event.currentTarget);
          }}
          slotProps={{
            toolbar: {
              showQuickFilter: true,
              quickFilterProps: { debounceMs: 500 },
            },
          }}
          initialState={{
            columns: {
              columnVisibilityModel: {
                keyword: false,
              },
            },
          }}
          columnVisibilityModel={{
            press: selectedPress === "ALL" ? true : false,
            filename: false,
          }}
        />
      </div>

      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        transitionDuration={0}
      >
        <MenuItem disableRipple className="scriptsListMenuItem">
          <ScriptsListComponent
            scriptsListState={scriptsListState}
            setSelectedScriptFile={setSelectedScriptFile}
            isLoading={isLoading}
          />
        </MenuItem>
      </Menu>
    </Grid>
  );
}
